.gameArea{
  position: relative;
  margin: 50px auto;
  width: 600px;
  height: 600px;
  border: 2px solid black;
}
.snakeDot{
  position: absolute;
  width: 2%;
  height: 2%;
  background-color: black;
  border: 1px solid #fff;
  z-index: 2;
}
.snakeFood{
  position: absolute;
  width: 2%;
  height: 2%;
  background: green;
  border: 1px solid gray;
  z-index: 1;
}